.App {
  text-align: center;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  padding-left: 20px;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  width:fit-content;
  color: #61dafb;
  border-color: #61dafb;
  text-decoration: none;
  padding: 5px;
  border-width: 3px;
  border-style: solid;
  border-radius:7px;
}

.App-link:hover{
  background-color: #61dafb;
  color:white;
  transition: 0.1s;
}
